
import fs from "fs"
import path from "path"
import { ProcurementDashboard, ProcurementItem } from "@/components/procurement-dashboard"
import { Header } from "@/components/header"

async function getData(): Promise<ProcurementItem[]> {
  const filePath = path.join(process.cwd(), 'procurement_analysis', 'idi_procurement_mvp.csv')

  if (!fs.existsSync(filePath)) {
    return []
  }

  const fileContent = fs.readFileSync(filePath, 'utf-8')
  const rows = fileContent.split('\n').filter(row => row.trim() !== '') // Remove empty rows

  // Headers: Product, Current_FOB, Pack_Size, Origin, Current_Landed_Cost, Weekly_Vol, ...
  // Skip header row
  const data = rows.slice(1).map(row => {
    // Basic CSV split
    const columns = row.split(',')

    // Map columns based on `run_analysis.py` output order for MVP CSV:
    // Product[0], Current_FOB[1], Pack_Size[2], Origin[3], Current_Landed_Cost[4], Weekly_Vol[5]

    const product = columns[0]
    const landedCost = parseFloat(columns[4]) || 0
    const weeklyVol = parseFloat(columns[5]) || 0

    // Calc Delta: Cost * Vol * 52 * 0.30
    const annualSpend = landedCost * weeklyVol * 52
    const delta = annualSpend * 0.30

    return {
      Product: product,
      Current_FOB: columns[1],
      Pack_Size: columns[2],
      Origin: columns[3],
      Current_Landed_Cost: landedCost,
      Weekly_Vol: weeklyVol,
      Potential_Savings: delta
    }
  })

  // Filter out any invalid rows
  return data.filter(item => item.Product && item.Product !== 'Product')
}

export default async function Home() {
  const items = await getData()

  return (
    <div className="flex min-h-screen flex-col bg-background">
      <Header />
      <main className="flex-1 p-8">
        <div className="flex items-center justify-between space-y-2 mb-8">
          <div>
            <h2 className="text-2xl font-bold tracking-tight">Procurement Opportunities</h2>
            <p className="text-muted-foreground">
              Top arbitrage targets identified by Anchor Marianas optimization layer.
            </p>
          </div>
        </div>

        <ProcurementDashboard items={items} />
      </main>
    </div>
  )
}
